<template>
  <div>
   <form class="form-signin text-center clearfix " style="width:460px;height: 300px;margin:200px auto;">
          <h1 class="h3 mb-5 font-weight-normal"> ZiMuZu.TV </h1>
         <input type="text" id="userName" class="form-control mb-3 w-75 float-left"  placeholder=""  @blur.prevent="checkName"  v-model="name" @focus="tip_name"><span class='bg-secondary text-white small float-left mt-1 ml-2'>{{tipName}}</span><span class='bg-danger text-white small float-left mt-1 ml-2'>{{doubleName}}</span>

         <input type="email" id="inputEmail" class="form-control mb-3 w-75 float-left" placeholder=""  @blur.prevent="checkEmail" v-model="email" ><span class='bg-danger text-white small float-left mt-1 ml-2'>{{doubleEmail}}</span>

         <input type="password" id="inputPassword" class="form-control mb-3 w-75 float-left" placeholder="" v-model="mima" value=""  @focus="tip_mima"> <span class='bg-secondary text-white small float-left mt-1 ml-2'>{{tipMima}}</span><span class='bg-danger text-white small float-left mt-1 ml-2'>{{tipPwd}}</span>
         
         <input type="password" id="resurePassword" class="form-control mb-3 w-75 float-left" placeholder=""  v-model="queren" > <span class='bg-danger text-white small float-left mt-1 ml-2'>{{rePwd}}</span>
         
         <span  class="bg-danger text-white small  mt-1 ml-2 float-left" v-show="tip">{{msg}}</span>
         
         <div class="clearfix ml-5" >
             <button class="btn btn-lg btn-primary btn-block w-50 " type="button" @click="handleReg"  data-toggle="modal" data-target="#exampleModal" ></button>
         </div>

         <!-- Modal -->
         <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
             <div class="modal-dialog" role="document">
                 <!--  -->
                 <div class="modal-content" v-if="isSucess">
                     <div class="modal-header"> <h6 class="modal-title" id="exampleModalLabel"></h6> <button type="button" class="close" data-dismiss="modal" aria-label="Close"> <span aria-hidden="true">&times;</span> </button> </div> <div class="modal-body mt-2 mb-2">   <span class="bg-warning text-white rounded ml-1 mr-1 pl-1 pr-1"></span></div> <div class="modal-footer"> <button type="button" class="btn btn-warning small" data-dismiss="modal" ></button> </div>
                 </div>
                 <!--  -->
                  <div class="modal-content" v-if="!isSucess">
                    <div class="modal-header"> <h6 class="modal-title" id="exampleModalLabel"></h6> <button type="button" class="close" data-dismiss="modal" aria-label="Close"> <span aria-hidden="true">&times;</span> </button> </div> <div class="modal-body mt-2 mb-2">   <span class="bg-primary text-white rounded ml-1 mr-1 pl-1 pr-1"></span></div> <div class="modal-footer"> <router-link to="/login"><button type="button" class="btn btn-primary small" data-dismiss="modal"></button></router-link> <button type="button" class="btn btn-danger small" data-dismiss="modal"></button> </div>  </div>
             </div>
         </div>

     </form>
  </div>
</template>

<script>
export default {
  name: 'register',
  components: {

  },
  data(){
      return{
            tipName:'',
            doubleName:'',
            doubleEmail:'',
            tipPwd:'',
            rePwd:'',
           isSucess:true,
           pwd:false,
           reck:false,
           msg:'',
           mima:'',
           isA:true,
           queren:'',
           name:'',
           tipMima:'',
           email:'',
           tip:false,   
           isSumit:false
      }
     
  },
  methods:{
    tip_name(){
      this.doubleName=''
      this.tipName="620"
    },
    tip_mima(){
      this.tipPwd=''
      this.tipMima="620"
    },
    checkName(){
        //1:
        var n = this.name;
        //2:ajax
        var url = "http://localhost:3000/existsName";
        url+="?userName="+n;
        this.axios.get(url).then(result=>{
          if(result.data.code > 0){
            this.isSumit = true;
          }else{
            this.tipName=''
            this.doubleName="!"
            this.isSumit = false;
          }

        })
        //2:
        //
        if(this.name.length>6&&this.name.length<20){
          this.doubleName=''
          this.tipName=''
           return;
        }
      },
      checkEmail(){
        var e = this.email;
        //2:ajax
         var reg=RegExp(/^\s*\w+@\w+.(com|cn)\s*$/);
        if(!reg.test(inputEmail.value)){
            this.doubleEmail=""
            this.isSumit = false;
            return
        }
        var url = "http://localhost:3000/existsEmail";
        url+="?inputEmail="+e;
        this.axios.get(url).then(result=>{
          if(result.data.code > 0){
            this.doubleEmail=''
            this.isSumit = true;
          }else{
            this.doubleEmail="!"
            this.isSumit = false;
          }
        })
        //
       
      },
       handleReg(){
        if(this.isSumit === false){ //
          return;
        }
        //1:  
        //  
        var n = this.name;
        var e=this.email;
        var p = this.mima;
        var p2 = this.queren;

        if(this.mima.length<6||this.mima.length>20){
          this.tipMima=''
          this.tipPwd="6-20"
          return;
        }else{
          this.tipMima=''
          this.tipPwd=""
        }
        //
        if(p != p2){
          this.rePwd=""
          return;
        }
        else{
          this.rePwd=""
        }
        //3:ajax
        var url = "http://localhost:3000/register?";
        url +="name="+n;
        url +="&email="+e;
        url +="&pwd="+p;
        url +="&resure="+p2
        this.axios.get(url).then(result=>{
            if(result.data.code>0) this.isSucess=false
        })
        //4:
      }
  }

}
</script>